<template>
    <v-container fluid>
        <v-card xs12 class="mb-1 explicador-bar-fixed">
            <v-list class="text-xs-center">
                <!--<h4>Seie > Perfil > Dados Pessoais</h4>-->
                <v-breadcrumbs divider="/">
                    <v-breadcrumbs-item >
                        <v-btn round flat color="white" class="grey--text" router to="/">
                            Home
                        </v-btn>

                    </v-breadcrumbs-item>

                    <v-breadcrumbs-item >
                        {{ name }}
                    </v-breadcrumbs-item>

                    <v-breadcrumbs-item  disabled>
                        Profile
                    </v-breadcrumbs-item>
                </v-breadcrumbs>
            </v-list>
            <v-list></v-list>
        </v-card>

        <!--Componente com informacao pessoal-->
        <personal-info :user="user"></personal-info>

        <!--Componente com informacao academica-->
        <academic-info :user="user"></academic-info>

        <!--Componente com as informacoes de localizacao-->
        <location-info :user="user"></location-info>


    </v-container>
</template>
<script>
    export default {
        data () {
            return {
                searchSubjects: true,
            }
        },
        computed: {
            loading () {
                return this.$store.getters.loading
            },
            allUsers () {
                return this.$store.getters.getUsers
            },
            user () {
                return this.$store.getters.user
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            name () {
                return this.isUser && this.user.name !== '' && this.user.name !== null ? this.user.name : 'Sem Nome'
            }
        },
        watch: {
            allUsers (value) {
                this.$store.dispatch('setProfileUser', this.$route.params.profile)
            },
            loading (value) {

                if (!value) { //busca os dados do perfil quando ele terminar de ler
                    this.$store.dispatch('setProfileUser', this.$route.params.profile)
                }
            }
        },
        beforeCreate () {
            this.$store.dispatch('setProfileUser', this.$route.params.profile)
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
//            alert('componente criado.')
//            console.log('rotas param: ', )
//            this.$Progress.finish()
        },
    }
</script>


