<template>
    <!--Informacoes do perfil-->
    <v-card class="mt-3">

        <v-card-title style="color: gray"><h3><i class="fas fa-graduation-cap  green--text"></i> Informacoes Academicas</h3></v-card-title>

        <v-container grid-list-sm class="pa-4">

            <!--Aqui serao mostrados os erros-->
            <v-layout row v-if="error">
                <v-flex xs12 sm6 offset-sm3>
                    <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                </v-flex>
            </v-layout>


            <!--Campos do formulario-->
            <v-layout row>

                <!--Campo Instituicao-->
                <v-flex xs11>

                    <v-text-field
                        prepend-icon="account_balance"
                        v-model="institution"
                        placeholder="Sua universidade">
                    </v-text-field>

                </v-flex>
                <v-flex xs1>
                    <add-education-info-dialog :propDialogType="'University'"></add-education-info-dialog>
                </v-flex>
            </v-layout>
            <v-layout row wrap>

                <!--Campo Curso -->
                <v-flex xs11 >
                    <v-text-field
                        prepend-icon="dns"
                        v-model="course"
                        type="text"
                        placeholder="Seu  Curso"
                    ></v-text-field>
                </v-flex>
                <v-flex xs1>
                    <add-education-info-dialog :propDialogType="'Course'"></add-education-info-dialog>
                </v-flex>

                <!--Campo disciplinas-->
                <v-flex xs11>
                        <v-select
                                label="Adicione aqui as suas disciplinas"
                                chips
                                tags
                                solo
                                append-icon=""
                                v-model="subjectNames"
                                class="elevation-0"
                        >
                            <template slot="selection" slot-scope="data">
                                <v-chip
                                        close
                                        @input="remove(data.item)"
                                        :selected="data.selected"
                                        title="Remover esta disciplina"
                                >
                                    <strong>{{ data.item }}</strong>
                                    <span>(Interresse)</span>
                                </v-chip>
                            </template>
                        </v-select>
                    <!--</template>-->

                </v-flex>
                <v-flex xs1 v-if="isMyProfile">
                    <add-education-info-dialog :propDialogType="'Subject'"></add-education-info-dialog>
                </v-flex>

            </v-layout>

        </v-container>

        <!--Botoes do formulario-->
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn v-if="isMyProfile" flat color="primary" @click="onCancel">Cancel</v-btn>

            <v-btn
                flat
                justify
                @click="onSave"
                :disabled="loading"
                v-if="isMyProfile"
                :loading="loading && showAcademicProcess">
                Gravar
                <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                </span>
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user'],
        data () {
            return {
                institution: '',
                course: '',
                subjects: '',
                showAcademicProcess: false,
                canceled: false,
                subjectNames: [],
            }
        },
        watch: {
            user (value) {

                if (value !== null && value !== undefined) {
                    this.inicializeUserInfo()
                }
            },
            loading (value) {

                if (!value && !this.error && this.showAcademicProcess) {

                    let text = "Os seus dados academicos foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showAcademicProcess = false
                }
            },
            subjectNamess (value) {
                this.subjectNames = this.subjectNamess
            }

        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            userIsAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            subjectNamess () {

                if (!this.isUser) return []

                return this.getUserSubjectNames(this.user)

            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        methods: {
            onSave () {

                //Procurar outra forma segura de verificar se o user tem net...
                if (!this.userAuth.hasOwnProperty('bio')) {

                    eventBus.$emit('semNet');

                    return
                }

                let user = this.userAuth

                user.Educations = {
                    institution:    this.institution,
                    course:         this.course,
                    subjects:         this.subjectsObject(),
                }
                this.showAcademicProcess = true
                this.$store.dispatch('updateOnlineUserData', user)
                // Chamar o metodo que vai gravar as disciplinas
                this.saveNewSubjectsToDB()

            },

            saveNewSubjectsToDB () {

                let vm = this
                this.subjectNames.forEach(function (selectSub) {

                    vm.$store.dispatch('saveSubject', {
                        name: selectSub,
                        creatorId: vm.userAuth.id,
                        created_at: new Date(),
                        confirmed: false
                    })


                })


            },

            getUserSubjectNames (user) {
                let subjNames = []

                if (user !== null && user.hasOwnProperty('Educations')  )
                {
                    let userSubjects = user.Educations.subjects

                    userSubjects.forEach(function (s) {

                        subjNames.push(s.name)

                    })
                }
                return subjNames
            },

            remove (item) {

                if (!this.isMyProfile) return //nao remova as disciplinas se o perfil nao for do user que esta a visualizar

                this.subjectNames.splice(this.subjectNames.indexOf(item), 1)
                this.subjectNames = [...this.subjectNames]
            },
            subjectsObject () {
                let subjects = this.user.Educations.subjects
                let finalSubject = []

                //Implementar aqui a busca das disciplinas selecionadas pelo user
                // para depois se retornar a lista com todas disciplinas

                this.subjectNames.forEach(function (selectSub) {

                    let sub = subjects.find(sub => {
                        return sub.name === selectSub
                    })

                    if (sub !== undefined && sub !== null) {

                        finalSubject.push(sub)

                    } else {

                        finalSubject.push({
                            name: selectSub,
                            status_explain: false,
                            status_like: true,
                            created_at: new Date(),
                        })
                    }

                })



                return finalSubject
            },
            onCancel () { //Este vai ser chamado antes de se comear a gravar, para ver se algum dado foi modificado...
                this.$store.dispatch('getAllUsers')
                this.$store.dispatch('setLoading', false)
                this.canceled = true
            },
            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },
            inicializeUserInfo () {
                const education = this.isUser ? this.user.Educations : {}

                this.subjectNames = this.subjectNamess
                this.institution = this.isUser && education ? education.institution : ''
                this.course = this.isUser && education ? education.course : ''
                this.subjects = !education ? '' : education.subjects && education.subjects[0] ? education.subjects[0].name : ''

            },

        },
        beforeCreate () {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {

            if (this.user !== null && this.user !== undefined) {

                this.inicializeUserInfo()

            }
        }
    }
</script>


