<template>
    <v-dialog v-model="dialog" width="350px">

        <!--Botao  que activa a dialog-->
        <v-btn
                flat
                fab
                color="blue"
                title="Clique para Adicionar nova"
                slot="activator"
                class="personalized-pr"
                @click.stop="dialog = !dialog"
        >
            <v-icon>add</v-icon>
            <!--&lt;!&ndash;<i class="fas fa-2x fa-street-view"></i>&ndash;&gt;-->
        </v-btn>

        <!--Comeca aqui o corpo principal da dialog-->
        <v-card>

            <!--Titulo da dialog-->
            <v-card-title color="red" class="primary pb-1 pt-1">
                <span>{{ dialogTitle }}</span>
                <v-spacer></v-spacer>
                <v-menu bottom left>
                    <v-btn icon slot="activator">
                        <v-icon>more_vert</v-icon>
                    </v-btn>

                    <!--Opcao de menu na barra do titulo da dialog-->
                    <v-list>
                        <v-list-tile @click="dialogType = 'University'">
                            <v-list-tile-title>Universidades</v-list-tile-title>
                        </v-list-tile>
                        <v-list-tile @click="dialogType = 'Course'">
                            <v-list-tile-title>Cursos</v-list-tile-title>
                        </v-list-tile>

                        <v-list-tile @click="dialogType = 'Subject'">
                            <v-list-tile-title>Disciplinas</v-list-tile-title>
                        </v-list-tile>
                    </v-list>
                </v-menu>
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4 mt-0 pt-0">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-layout row wrap class="mt-0">

                    <!--Campo de insercao do nome da disciplinas, curso ou instituicao-->
                    <v-flex xs10>

                        <v-text-field
                            name="institution"
                            :label="textFieldLabel"
                            v-model="description"
                            type="text"
                            required></v-text-field>
                    </v-flex>

                    <!--Botao gravar, ao lado do campo-->
                    <v-flex xs1>
                        <v-btn
                            @click="onSave"
                            :disabled="loading"
                            v-if="isUserAuth"
                            :loading="loading"
                            title="Gravar" flat fab class="mr-4 mr-1 primary--text">
                            <v-icon>check_circle</v-icon>
                            <span slot="loader" class="custom-loader">
                                <v-icon light>cached</v-icon>
                            </span>
                        </v-btn>

                    </v-flex>

                    <!--Instituicoes serao listados aqui-->

                    <v-flex xs12>

                        <!--Conteudo visualizado quando seleciona-se universidades-->
                        <v-list class="pl-0" v-if="dialogType === 'University'">
                            <div v-for="item in institions">
                                <v-divider></v-divider>
                                <v-list-tile @click="description = item.name" class="pl-0 ml-0">
                                    <v-list-tile-content>
                                        <v-list-tile-title>
                                            {{ item.name }}
                                        </v-list-tile-title>
                                    </v-list-tile-content>
                                    <!--<v-list-tile-action>-->
                                        <!--<v-btn icon ripple>-->
                                            <!--<v-icon color="grey lighten-1">info</v-icon>-->
                                        <!--</v-btn>-->
                                    <!--</v-list-tile-action>-->
                                </v-list-tile>
                            </div>

                        </v-list>

                        <!--Conteudo visualizado quando seleciona-se Cursos-->
                        <v-list class="pl-0" v-if="dialogType === 'Course'">
                            <div v-for="item in courses">
                                <v-divider></v-divider>
                                <v-list-tile @click="description = item.name" class="pl-0 ml-0">
                                    <v-list-tile-content>
                                        <v-list-tile-title>
                                            {{ item.name }}
                                        </v-list-tile-title>
                                    </v-list-tile-content>
                                    <!--<v-list-tile-action>-->
                                        <!--<v-btn icon ripple>-->
                                            <!--<v-icon color="grey lighten-1">info</v-icon>-->
                                        <!--</v-btn>-->
                                    <!--</v-list-tile-action>-->
                                </v-list-tile>
                            </div>

                        </v-list>

                        <!--Conteudo visualizado quando seleciona-se Subjects-->
                        <v-list class="pl-0" v-if="dialogType === 'Subject'">
                            <div v-for="item in subjects">
                                <v-divider></v-divider>
                                <v-list-tile @click="description = item.name" class="pl-0 ml-0">
                                    <v-list-tile-content>
                                        <v-list-tile-title>
                                            {{ item.name }}
                                        </v-list-tile-title>
                                    </v-list-tile-content>
                                    <v-list-tile-action v-if="isMySubject(item)">

                                        <v-menu bottom left>
                                            <v-btn icon ripple slot="activator">
                                                <v-icon>more_vert</v-icon>
                                            </v-btn>

                                            <!--Opcao de menu na barra do titulo da dialog-->
                                            <v-list>
                                                <v-tooltip right>
                                                    <v-list-tile slot="activator" @click="onExplain(item)">
                                                        <v-list-tile-title>{{ item.status_explain ? 'Nao' : '' }} {{ item.status_explain }} Explico</v-list-tile-title>
                                                    </v-list-tile>
                                                    <span v-if="item.status_explain">Nao explico esta disciplina</span>
                                                    <span v-else>Adicionar nas Disciplinas que explico</span>
                                                </v-tooltip>

                                                <v-tooltip right>
                                                    <v-list-tile slot="activator" @click="onLike(item)">
                                                        <v-list-tile-title>{{ item.status_like ? 'Nao' : '' }} {{ item.status_like }} Gosto</v-list-tile-title>
                                                    </v-list-tile>
                                                    <span v-if="item.status_like">Remover da lista das disciplina que gosto</span>
                                                    <span v-else>Adicionar como umas das preferidas</span>
                                                </v-tooltip>
                                                <!--<v-tooltip right>-->
                                                    <!--<v-list-tile slot="activator" @click="deleteSubject(item)">-->
                                                        <!--<v-list-tile-title>Deletar</v-list-tile-title>-->
                                                    <!--</v-list-tile>-->
                                                    <!--<span>Deletar</span>-->
                                                <!--</v-tooltip>-->
                                            </v-list>
                                        </v-menu>

                                    </v-list-tile-action>
                                </v-list-tile>
                            </div>

                        </v-list>
                    </v-flex>

                </v-layout>

            </v-container>


            <v-divider class="mb-2"></v-divider>

            <v-card-actions class="pt-0 pb-0 mb-0" >
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel">Cancelar</v-btn>
                <v-btn
                    flat
                    @click="onSave"
                    class="green--text"
                    :disabled="loading"
                    v-if="isUserAuth"
                    :loading="loading">
                    Gravar
                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>

        </v-card>
    </v-dialog>

</template>
<script>
    import { eventBus } from '../../main'
    export default {
        created () {
            this.dialogType = this.propDialogType
        },
        props: ['propDialogType'],
        data () {
            return {
                dialog: false,
                canceled: false,
                description: '',
                dialogType: 'University',
                dialogTitle: 'Cadastre a sua Universidade',
                textFieldLabel: 'Escreva aqui a sua Universidade',
                actionForSave: 'saveInstitution',
                snackTextSucess: 'Universidade gravada com sucesso!',
                snackTextError: 'Por favor, Insira uma Universidade valida entes de gravar!',
                items: [],
            }
        },

        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            institions () {
                return this.$store.getters.getInstitutions
            },

            courses () {
                return this.$store.getters.getCourses
            },
            subjects () {
                return this.$store.getters.getSubjects
            },
        },

        watch: {

            dialog (value) {

                this.$store.dispatch('setLoading', false)

                if (value) {

                    this.dialogType = this.propDialogType

                }
            },
            loading (value) {

                if (!value && !this.error && this.dialog) {

                    let color = 'green'
                    eventBus.$emit('showSnackbar', this.snackTextSucess, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.description = ''
                }
            },

            // Este controla continuamente a opcao do menu selecionada na dialog
            dialogType (value) {

                if (value === 'University')
                {

                    this.dialogTitle = 'Cadastre a sua Universidade'
                    this.textFieldLabel = 'Escreva aqui a sua Universidade'
                    this.snackTextSucess = 'Universidade gravada com sucesso!'
                    this.snackTextError = 'Por favor, Insira uma Universidade valida entes de gravar!',
                    this.actionForSave = 'saveInstitution'
                    this.description = ''
                    this.items = this.institions

                }
                else if (value === 'Course')
                {
                    this.dialogTitle = 'Cadastre o seu curso'
                    this.textFieldLabel = 'Escreva aqui o sua Curso'
                    this.snackTextSucess = 'Curso gravado com sucesso!'
                    this.snackTextError = 'Por favor, Insira um Curso valido entes de gravar!',
                    this.actionForSave = 'saveCourse'
                    this.description = ''
                    this.items = this.courses

                }
                else if (value === 'Subject')
                {
                    this.dialogTitle = 'Cadastre as suas Disciplinas'
                    this.textFieldLabel = 'Escreva aqui a disciplina'
                    this.snackTextSucess = 'Disciplina gravada com sucesso!'
                    this.snackTextError = 'Por favor, Insira uma Disciplina valida entes de gravar!',
                    this.actionForSave = 'saveSubject'
                    this.description = ''

                }

            }

        },

        methods: {

            userHasInstitution () { // Verifica se o user ja possui a instituicao que ele mesmo acabou de registar para nao voltar a gravar desnecessariamente

                return this.userAuth.Educations && this.userAuth.Educations.institution && this.userAuth.Educations.institution === this.description
            },

            userHasCurse () { // Verifica se o user ja possui o curso que ele mesmo acabou de registar para nao voltar a gravar desnecessariamente
                return this.userAuth.Educations && this.userAuth.Educations.course && this.userAuth.Educations.course === this.description
            },

            userHasSubject () { // Verifica se o user ja possui a disciplina que ele mesmo acabou de registar para nao voltar a gravar desnecessariamente
                
                if (this.userAuth.Educations && this.userAuth.Educations.subjects)
                {
                    let subjects = this.userAuth.Educations.subjects
                    
                    return subjects.find((subject) => {
                        return subject.name === this.description
                    })
                    
                }
                return false
            },

            onSave () {

                // Verificando se o user preencheu o que ele quer registar
                if (this.description.length  < 5) {

                    eventBus.$emit('showSnackbar', this.snackTextError, 'red', 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente
                    return

                }

                //Procurar outra forma segura de verificar se o user tem net...
                if (!this.userAuth.hasOwnProperty('bio')) {

                    eventBus.$emit('semNet');

                    return
                }


                // Verificando se a instituicao, curso ou disciplina o user ja possui ou nao
                if (this.userHasInstitution() || this.userHasCurse() || this.userHasSubject())
                {
                    //Se ele ja tiver nao precisamos gravar mais
                    eventBus.$emit('showSnackbar', this.snackTextSucess, 'blue'); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.description = ''
                    this.dialog = false
                    return
                }
                let userToSave = this.updatedUserToSave() //chamando o metodo que vai nos dar o objecto certo do user a ser gravado

                //Este vai gravar Universidade, Curso ou disciplina, veja a variavel: actionForSave
                this.$store.dispatch(this.actionForSave, {name: this.description, creatorId: this.userAuth.id, created_at: new Date(), confirmed: false})
//
                this.$store.dispatch('updateOnlineUserData', userToSave)

            },

            onExplain (subject) {

                let user = this.userAuth
                let subjects = this.userAuth.Educations.subjects
                let finalSubjects = []
                subjects.forEach(function (s) {

                    if (s.name === subject.name) {

                        finalSubjects.push({
                            created_at: s.created_at,
                            name: s.name,
                            status_explain: !s.status_explain,
                            status_like: s.status_like,
                        })

                    } else {
                        finalSubjects.push(s)
                    }

                })

                user.Educations = {
                    institution: user.Educations.institution,
                    course: user.Educations.course,
                    subjects: finalSubjects,
                }

                this.$store.dispatch('updateOnlineUserData', user)

            },

            onLike (subject) {

                let user = this.userAuth
                let subjects = this.userAuth.Educations.subjects
                let finalSubjects = []
                subjects.forEach(function (s) {

                    if (s.name === subject.name) {

                        finalSubjects.push({
                            created_at: s.created_at,
                            name: s.name,
                            status_explain: s.status_explain,
                            status_like: !s.status_like,
                        })

                    } else {
                        finalSubjects.push(s)
                    }

                })

                user.Educations = {
                    institution: user.Educations.institution,
                    course: user.Educations.course,
                    subjects: finalSubjects,
                }

                this.$store.dispatch('updateOnlineUserData', user)

            },

            isMySubject (subject) {

                let userSubjects = this.userAuth.Educations.subjects

                let isM = userSubjects.find(sub => {
                    return sub.name === subject.name
                })

                return isM !== null && isM !== undefined

            },

            onCancel () { //Este vai ser chamado antes de se comear a gravar, para ver se algum dado foi modificado...
                this.$store.dispatch('setLoading', false)
                this.canceled = true
                this.dialog = false
            },

            updatedUserToSave () {

                let user = this.userAuth
                let userEducation = {
                    institution: this.description,
                    course: user.Educations.course,
                    subjects: user.Educations.subjects,
                }

                if (this.dialogType === 'University')
                {
                    user.Educations = { //Gravando apenas a instituicao do user
                        institution: this.description,
                        course: user.Educations.course,
                        subjects: user.Educations.subjects,
                    }
                }
                else if (this.dialogType === 'Course')
                {
                    user.Educations = { //Gravando apenas a instituicao do user
                        institution: user.Educations.institution,
                        course: this.description,
                        subjects: user.Educations.subjects,
                    }
                }
                else if (this.dialogType === 'Subject')
                {
                                //Disciplina a gravar para o user
                    const subj = { name: this.description, status_explain: false, status_like: true, created_at: new Date()}

                    let userSubjects = this.userAuth.Educations.subjects
                    userSubjects.push(subj)

                    user.Educations = { //Gravando apenas a instituicao do user
                        institution: user.Educations.institution,
                        course: user.Educations.institution,
                        subjects: userSubjects,
                    }

                }


                return user
            },

            // Meodo chamado quando se clica no dissmiss do erro que eh lancado como retorno da base de dados
            onDismissed () {
                this.$store.dispatch('clearError')
            },

            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },


        },

        filters: {
            limit (str) {
                return str.substring(0, 10)
            }
        }
    }
</script>
<style>


</style>


