<template>
    <div>
        <template>
           <v-layout justify-center right fill-height>
               <v-btn
                       fab
                       bottom
                       left
                       flat
                       round
                       color="red"
                       title="Ocultar "
                       slot="activator"
                       @click.stop="closeDrawer"
                       v-if="temporary"
               >
                   <v-icon>close</v-icon>
                   <!--<i class="fas fa-2x fa-street-view"></i>-->
               </v-btn>
           </v-layout>
        </template>
        <v-list dense>
            <template>
                <!--Select da disciplina encima no drawer esquerdo-->
                <v-layout justify-center fill-height v-if="isHomePage">
                    <v-flex xs11>
                        <v-select
                                v-bind:items="tipos"
                                v-model="e2"
                                label="Select"
                                single-line
                                auto
                                prepend-icon="map"
                                hide-details
                        ></v-select>
                    </v-flex>
                </v-layout>

                <!--Campo de pesquisa encima no drawer esquerdo-->
                <v-layout v-if="isHomePage">
                    <v-flex>
                        <v-text-field
                                light
                                solo
                                v-model="searchedData"
                                prepend-icon="search"
                                :placeholder="'Pesquisar ' + comp_searchedData"
                                class="elevation-0"
                                style="max-width: 400px; min-width: 128px"
                        ></v-text-field>
                    </v-flex>
                </v-layout>

                <!--Todo: Primeiro divider que poe a linha de divisao-->
                <v-divider v-if="isHomePage"></v-divider>


                <!--Opcao de configuracao que aparece em baixo da lista-->
                <template v-if="filtering !== ''">
                    <v-divider></v-divider>
                    <v-list-tile class="primary" :title="'Apenas explicadores de ' + filtering">

                        <v-list-tile-content class="ml-5">
                            <v-list-tile-title>
                                {{ filtering }}
                            </v-list-tile-title>
                        </v-list-tile-content>
                        <v-list-tile-action title="Apagar o filtro" @click="onCloseFilter" style="cursor: pointer">
                            <v-icon>close</v-icon>
                        </v-list-tile-action>
                    </v-list-tile>
                </template>

                <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                <div v-for="item in items">
                    <template>
                        <v-list-tile @click="onSelect(item)" :title="'Ver explicadores de ' + item.name">
                            <v-list-tile-action>
                                  <v-icon>help</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>
                                    {{ item.name }}
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>

                <!--Notificacao que aparece quando nao ha resuultados de pesquisa.-->
                <div v-if="items.length === 0">
                    <template>
                        <v-list-tile @click.prevent="">
                            <v-list-tile-action>
                                <v-icon class="red--text">youtube_searched_for</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title class="red--text">
                                    Sem resultados . . .
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>

                <template>
                    <v-divider></v-divider>
                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>settings</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>
                                Configuracoes
                            </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </template>

                <!--Botao para o envio de sugestao, que aparece apenas para os users autenticados-->
                <div v-if="isUserAuth">
                   <sugestion-dialog></sugestion-dialog>
                </div>

            </template>

            <!--Todo: Segundo divider que poe a linha de divisao-->
            <v-divider></v-divider>
        </v-list>
    </div>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['temporary'],
        created () {
            eventBus.$on('resetExplicadoresFilter', () => {

                this.filtering = ''
                this.searchedData = ''
                this.e2 = ''

            })
        },
        data: () => ({
            searchedData: '',
            filtering: '',
            e2: ''
        }),
        computed: {
            comp_searchedData ()  {
                return this.e2;
            },
            tipos() {
                return this.$store.getters.getTipos
            },
            subjects () {
                return this.$store.getters.getSubjects
            },
            courses () {
                return this.$store.getters.getCourses
            },
            institutions () {
                return this.$store.getters.getInstitutions
            },
            items ()
            {
                if (this.comp_searchedData === '' || this.comp_searchedData === 'Disciplinas') return this.searchedSubjects
                if (this.comp_searchedData === 'Cursos') return this.searchedCourses
                if (this.comp_searchedData === 'Universidades') return this.searchedInstitutions
            },
            searchedSubjects ()
            {
                if (this.searchedData === '') return this.subjects

                return this.subjects.filter(s => {
                    return s.name.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedCourses ()
            {
                if (this.searchedData === '') return this.courses

                return this.courses.filter(c => {
                    return c.name.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            searchedInstitutions ()
            {
                if (this.searchedData === '') return this.institutions

                return this.institutions.filter(i => {
                    return i.name.toLowerCase().includes(this.searchedData.toLowerCase())
                })
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
        },
        watch: {
            comp_searchedData (value) {
                this.searchedData = ''
            }
        },
        methods: {
            onSelect (item)
            {
                this.filtering = item.name
                eventBus.$emit('filterExplicadoresBy', item, this.comp_searchedData);
            },
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                eventBus.$emit('resetExplicadoresFilter');
            },
            closeDrawer () {
                eventBus.$emit('closeDrawer', 'left')
            }

        }
    }
</script>


