<template>
    <v-container>
        <v-layout row v-if="error">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>
                <v-card>
                    <v-card-text>
                        <h2 class="ml-3" style="color: darkgray; cursor: pointer" title="Se desejar podes actualizar a sua senha">
                            Actualizacao de senha
                            <v-icon right dark class="green--text">check_circle</v-icon>
                        </h2>

                        <!--Informacao sobre nao necessidade de actualizar a senha para google-->
                        <v-card-title
                            class="green--text"
                            v-if="authUserProvider === 'google'"
                        >
                            <span><i class="fab fa-google-plus-g"></i></span>
                            Nao precisa actualizar senha pois voce criou a sua conta usando google,
                            basta apenas continuar a usar este email usando google para fazer login
                        </v-card-title>
                        <v-card-title
                            class="green--text"
                            v-if="authUserProvider === 'facebook'"
                        >
                            <span><i class="fab fa-facebook"></i></span>Nao precisa actualizar senha pois voce criou a sua conta usando facebook,
                            basta apenas continuar a usar este email usando facebook para fazer login
                        </v-card-title>
                    </v-card-text>
                </v-card>

                <!-- Campos do formulario -->
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">

                                <!--Campo email do user-->
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="email"
                                                label="Mail"
                                                prepend-icon="mail"
                                                id="email"
                                                v-model="email"
                                                type="email"
                                                disabled
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <!--Password anterior-->
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="previousPassword"
                                                label="Password Actual"
                                                prepend-icon="lock_outline"
                                                id="previousPassword"
                                                :disabled="desableFields"
                                                v-model="previousPassword"
                                                type="password"
                                                ></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <!--Nova Password-->
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="password"
                                                label="Nova Password"
                                                prepend-icon="lock"
                                                id="password"
                                                :disabled="desableFields"
                                                v-model="password"
                                                type="password"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <!--Confirmacao da Nova Password-->
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="confirmPassword"
                                                label="Confirme a nova Password"
                                                prepend-icon="lock"
                                                :disabled="desableFields"
                                                id="confirmPassword"
                                                v-model="confirmPassword"
                                                type="password"
                                                :rules="[comparePasswords]"></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <!--Botoes do formulario-->
                                <v-layout row>
                                    <v-flex xs12>

                                        <v-btn
                                            round
                                            @click="resetFields"
                                            :disabled="desableFields">

                                            Cancelar
                                            <v-icon right dark class="red--text">highlight_off</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                        <v-btn
                                            round
                                            type="submit"
                                            :disabled="loading || desableFields"
                                            :loading="loading">
                                            Gravar
                                            <v-icon right dark class="green--text">check_circle</v-icon>
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>
                                </v-layout>
                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>

            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        beforeCreate () {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
            this.email = this.isUserAuth ? this.userAuth.email : ''
        },
        data () {
            return {
                email: '',
                previousPassword: '',
                password: '',
                confirmPassword: ''
            }
        },
        watch: {

            userAuth (value) {
                if (this.email !== '' && this.email !== null && this.email !== undefined)
                    return

                this.email = this.isUserAuth ? this.userAuth.email : ''

            },
            loading (value) {
                if (!value && !this.error) {

                    let text = "Password actualizado com sucesso."
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.resetFields()
                }
            }

        },
        computed: {
            comparePasswords () {
                return this.password !== this.confirmPassword
                    ? 'Senha de confirmacao nao corresponde' : ''
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            authUserProvider () {
                return this.$store.getters.getAuthProvider
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            desableFields () {
                return this.authUserProvider === 'google' || this.authUserProvider === 'facebook'
            }
        },
        methods: {
            onSignup () {
                this.$store.dispatch('updateUserPassword', {email: this.email, previousPassword: this.previousPassword, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            resetFields () {
                this.previousPassword = ''
                this.password = ''
                this.confirmPassword = ''
            }
        },

    }
</script>


