<template>
    <v-dialog v-model="dialog" width="350px">
        <v-btn
                fab
                bottom
                right
                fixed
                flat
                round
                outline
                color="blue"
                title="Enviar Sugestoes"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            <v-icon>mail_outline</v-icon>
            <!--<i class="fas fa-2x fa-street-view"></i>-->
        </v-btn>
        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text py-4 title">
                Envie-nos a sua sugestao
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4 mb-5">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-layout row wrap class="mt-0">
                    <v-flex xs12>
                        <v-text-field
                                name="Sugestion"
                                label="Escreva aqui a sua sugestao"
                                rows="4"
                                multi-line
                                v-model="sugestion"
                                type="text"
                                required></v-text-field>
                    </v-flex>
                    <v-flex xs12 class="mt-0">
                        <v-btn block @click="onPickFile">Carregar imagem</v-btn>
                        <input
                                type="file"
                                style="display: none"
                                ref="fileInput"
                                accept="image/*"
                                @change="onFilePicked">
                    </v-flex>
                    <v-card xs12 height="180px">
                        <v-flex xs12 v-if="imageUrl !== ''">
                            <img style="width: 100%" :src="imageUrl" height="175">
                        </v-flex>
                    </v-card>
                </v-layout>
            </v-container>

            <v-divider class="mb-2"></v-divider>
            <v-card-actions class="pt-0">
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                <v-btn
                    flat
                    @click="sendSugestion"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading">
                    Enviar
                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                dialog: false,
                sugestion: '',
                imageUrl: '',
                image: null
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.dialog) {

                    let text = "Enviada com sucesso, apreciamos imenso o seu parecer..."
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.sugestion = ''
                    this.imageUrl = ''
                }
            }

        },

        methods: {
            sendSugestion () {

                if (this.sugestion.length  < 5) {

                    let text = "Por favor, Insira uma sugestao valida entes de enviar!"
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color,5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente
                    return

                }

                this.$store.dispatch('sendSugestion', {sugestion: this.sugestion,creatorId: this.userAuth.id, image: this.image,})

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            mostrarDialog () {
                this.dialog = !this.dialog
            },
            onPickFile ()  {
                this.$refs.fileInput.click()
            },
            onFilePicked (event) {
                const files = event.target.files
                let filename = files[0].name
                if (filename.lastIndexOf('.') <= 0)
                {
                    return alert('Por favor adicione um ficheiro valido!')
                }
                const fileReader = new FileReader()
                fileReader.addEventListener('load', () => {
                    this.imageUrl = fileReader.result
                })
                fileReader.readAsDataURL(files[0])
                this.image = files[0]
            }
        }
    }
</script>


