import * as firebase from 'firebase'
import auth_store from '../auth/index'
export default {
    state: {
        loading: false,
        error: null,
        not_auth_img: '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png',
        showSideMenus: false,
        main_menu: [
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: false, icon: 'help', text: 'Disciplina 01' },
            { aditional: true, icon: 'settings', text: 'Configuracoes' },
            { aditional: true, icon: 'add', text: 'Adicionar nova ' },
        ],
        profile_main_menu: [

            { aditional: false, icon: 'help', text: 'Dados Pessoais' },
            { aditional: false, icon: 'help', text: 'Dados academicos' },
            { aditional: false, icon: 'help', text: 'Localizacao' },

        ],
        profile_menu_right: [
            { aditional: false, icon: 'help', text: 'Disciplinas que explica' },
            { aditional: false, icon: 'help', text: 'O que dizem sobre ele' },
            { aditional: false, icon: 'help', text: 'Sua disponibilidade' },
            { aditional: false, icon: 'help', text: 'Seus Estudantes' },
        ],
        homePage: true,
        loginPage: false,
        registerPage: false,
        closeLoginDialog: false,

        // Informacao de dialog de confirmacao
        globalConfirmDialogDetails: { show: false }, // Objecto com todas informacoes de confirmacao

        didUserConfirm: false, //Quando a dialog abre, o user confirma, e guardamos aqui a resposta

        tipos: [
            'Disciplinas','Cursos','Universidades'
        ],
        auth_dropdown: [
            {hasLink: true, icon: 'tag_faces', title: 'Perfil', link: 'profile' },
            {hasLink: true, icon: 'settings', title: 'Configuraes', link: '/settings' },
            {hasLink: true, icon: 'help', title: 'Ajuda', link: '/help' },
            {hasLink: false, icon: 'input', title: 'Log out', link: false }
        ],
        not_auth_dropdown: [
            {hasLink: true, icon: 'account_circle', title: 'Login', link: '/login' },
            {hasLink: true, icon: 'supervisor_account', title: 'Registo ', link: '/register' },
            {hasLink: true, icon: 'help', title: 'Ajuda', link: '/help' },
        ]
    },
    mutations: {
        togleHome (state, value) {
            state.homePage = value
            state.loginPage = !value
            state.registerPage = !value
        },
        togleLogin (state, value) {
            state.loginPage = value
            state.homePage = !value
            state.registerPage = !value
        },
        togleRegister (state, value) {
            state.registerPage = value
            state.loginPage = !value
            state.homePage = !value
        },
        togleSideMenus (state,value) {
            state.showSideMenus = value
        },
        setLoading (state, value) {
            state.loading = value
        },
        setError (state, value) {
            state.error = value
        },
        clearError(state) {
            state.error = null
        },

        globalConfirmDialogDetails(state, value) {
            state.globalConfirmDialogDetails = value
        },

        userConfirmed(state,value) {
            state.didUserConfirm = value
        },
    },
    getters: {
        getMenuOptions (state) {
            return state.homePage ? state.main_menu : state.profile_main_menu
        },
        getMenuRightSide (state) {
            return state.profile_menu_right
        },
        isHomePage (state) {
            return state.homePage
        },
        isLoginPage (state) {
            return state.loginPage
        },
        isRegisterPage (state) {
            return state.registerPage
        },
        isSideMenusShowing (state) {
            return state.showSideMenus
        },
        isUserAuth () {
            return auth_store.state.userAuth !== null  && auth_store.state.userAuth !== undefined
        },
        getDropdownItems (state,getters) {
            return getters.isUserAuth ? state.auth_dropdown : state.not_auth_dropdown
        },
        getTipos (state) {
            return state.tipos
        },
        loading (state) {
            return state.loading
        },
        error (state) {
            return state.error
        },
        getImg (state, getters) {

            return getters.isUserAuth && getters.userAuth.photoURL ?
                getters.userAuth.photoURL :
                getters.isUserAuth ?
                state.auth_img : state.not_auth_img
        },

        // Informacoes da dialog de confirmacao global
        globalConfirmDialogDetails (state) {
            return state.globalConfirmDialogDetails
        },

        didUserConfirm (state) {
            return state.didUserConfirm
        }

    },

    actions: {
        togleHomePage ({commit}, value) {
            commit('togleHome',value)
        },
        togleLoginPage ({commit}, value) {
            commit('togleLogin',value)
        },
        togleRegisterPage ({commit}, value) {
            commit('togleRegister',value)
        },
        togleSideMenus ({commit}, value) {
            commit('togleSideMenus',value)
        },
        clearError ({commit}) {
            commit('clearError')
        },

        setLoading ({commit}, value) {
            commit('setLoading', value)
        },

        togleConfirmDialog ({commit}, value) {

            commit('globalConfirmDialogDetails', value)

        },



        userConfirmation ({commit}, value) {
            commit('userConfirmed', value)
        },

    },
}


