<template>
    <div>
        <v-card>
            <div class="text-xs-center">
                <h1 >Todas as suas configuracoes</h1>
            </div>

            <!-- Usando o componente no settings, para actualizar a senha-->
            <update-password></update-password>

            <!--Componente para actualizacao das disciplinas pessoais-->
            <my-subjects-update></my-subjects-update>

            <div class="text-xs-center mb-5" style="color: gray">
                <h3>Tens de confirmar o seu email</h3>
            </div>

            <br>

            <div class="text-xs-center mb-5" style="color: gray">
                <h3>Deseja alterar o seu email? aqui pode</h3>
            </div>

            <br>


        </v-card>

    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data() {
            return {
                e6: 1,
                loader: null,
            }
        },
        watch: {

        },
        beforeCreate() {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(70)
        },
    }
</script>
<style>

