<template>
    <v-dialog v-model="dialog" width="350px">
        <v-btn
                fab
                bottom
                right
                color="pink"
                dark
                fixed
                title="Entrar"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            <v-icon>account_circle</v-icon>
        </v-btn>
        <v-card>
            <v-card-title
                    class="lighten-4 py-4 title"
            >
                Faca Login
            </v-card-title>
            <v-divider></v-divider>
            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>
                <v-layout row wrap class="mt-5">
                    <v-flex xs12>
                        <v-text-field
                                name="email"
                                label="Mail"
                                prepend-icon="mail"
                                id="email"
                                v-model="email"
                                type="email"
                                required></v-text-field>
                    </v-flex>
                    <v-flex xs12>
                        <v-text-field
                                name="password"
                                label="Password"
                                prepend-icon="lock"
                                id="password"
                                v-model="password"
                                type="password"
                                required></v-text-field>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn flat color="primary" @click="registarOpt">Registar-se</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                <v-btn
                    flat
                    @click="onLogin"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Entrar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: false,
                email: '',
                password: '',
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            didUserConfirm () {
                return this.$store.getters.didUserConfirm
            },
        },
        watch: {
            didUserConfirm (value) {

                if (this.isUserAuth && value) {
                    this.$store.dispatch('logOutUser')
                    this.dialog = !this.dialog
                    this.$store.dispatch('userConfirmation', false) //Resetando a confirmacao do user
                }

            }
        },
        methods: {
            onLogin () {
                this.$store.dispatch('loginUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },

            /**
             * Metodo que determina se a dialog de login pode ser mostrada ou nao, caso nao, mostra a dialog de
             * confirmacao de enceramento de sessao
             *
             */
            mostrarDialog () {
                if (!this.isUserAuth) {
                    this.dialog = !this.dialog
                    return
                }

                const title = 'Voc est autenticado'
                const message = 'Deseja fechar a sesso actual para poder autenticar-se novamente?'
                const btn1 = 'No'
                const btn2 = 'Sim'

//              Chamando a tela de confirmacao global, que eh controlado pelo watch acima. para fazer a tarefa confirmada.
                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})

            },
            registarOpt () {
                this.dialog = false
                this.$router.push('/register')
            }
        }
    }
</script>


