import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import ExplicadorIndex from '@/components/profiles/Index'
import Login from '@/components/auth/Login'
import Register from '@/components/auth/Register'
import EmailVerified from '@/components/auth/EmailVerified'
import Help from '@/components/shared/Help'
import Settings from '@/components/settings/Settings'
import TesteRoute from '@/components/Teste'

// Routers Guards
import HomeGuard from './guards/home-guard'
import LoginGuard from './guards/login-guard'
import RegisterGuard from './guards/register-guard'
import ProfileGuard from './guards/profile-guard'
import HelpGuard from './guards/help-guard'
import SettingsGuard from './guards/settings-guard'

Vue.use(Router)

export default new Router({
  routes: [
      {
          path: '/',
          name: 'Home',
          component: Home,
          beforeEnter: HomeGuard
      },
      {
          path: '/:profile/profile',
          name: 'Profile',
          component: ExplicadorIndex,
          beforeEnter: ProfileGuard
      },
      {
          path: '/login',
          name: 'Login',
          component: Login,
          beforeEnter: LoginGuard
      },
      {
          path: '/register',
          name: 'Register',
          component: Register,
          beforeEnter: RegisterGuard
      },
      {
          path: '/help',
          name: 'Help',
          component: Help,
          beforeEnter: HelpGuard
      },
      {
          path: '/settings',
          name: 'Settings',
          component: Settings,
          beforeEnter: SettingsGuard
      },
  ],
    mode: 'history'
})



