<template>
    <div>

        <div class="text-xs-center mt-5">
            <h1 >Email verificado com sucesso!</h1>
            <br>
            <br>
            <h4 class="green--text">Parabens o seu endereco de email foi  verificado com sucesso!</h4>
        </div>

    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        mounted () {
            let rota = this.$router.currentRoute.fullPath
            let params = this.$router.currentRoute.params
            console.log('rota: ' + rota)
            console.log('parametros da rota: ' + params)
        },
        beforeCreate() {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(70)
        },
    }
</script>
<style>

</style>


