<template>
    <div  style="background-color: white; min-height: 600px" class="elevation-2">
        <div class="text-xs-center">
            <h1 class="pt-3">Help Page (explicador)</h1>
            <h4 class="mt-1">Pagina com a informacao e tutoriais do explicador</h4>

            <h2 class="mt-4">Introducao</h2>
            <p>Aproveite toda a ajuda disponivel, para poder explorar as variadas funcionalidades disponiveis na sua rede social</p>

            <h2 class="mt-4">Como criar conta?</h2>
            <p>Para criar conta  muito simples, basta apenas clicar na barra de cima, no icon com uma imagem redonda, no dropdown que ira aparecer com 3 opcoes, escolha a opcao criar conta</p>
            <p>De seguida insira o seu email, a senha e depois confirme a senha. Ao clicar em criar, o sistema vai processar o seu pedido, se houver algum erro,
                o mesmo sera reportado em ingles, <br>Bem acima abaixo ou acima do titulo do formulario. Caso os seus dados estiverem correctos, a
                sua conta sera criar e de seguida seras redirecionado para a pagina home. NOTA: Logo que a sua conta for criada, iremos lhe envar automaticamente um email de confirmacao de registo.
                Por favor, aceda o seu email e clique no link de confirmacao para confirmar o seu email. Caso nao tiver o seu email confirmado, algumas funcionalidades nao estarao activas</p>
        </div>

    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data() {
            return {
                loader: null,
                loading: false,
                loading2: false,
                loading3: false,
                loading4: false,
            }
        },
        watch: {
            loader() {
                const l = this.loader
                this[l] = !this[l]

                setTimeout(() => (this[l] = false), 3000)

                this.loader = null
            }
        },
        beforeCreate() {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(70)


        },
        mounted () {
            console.log(this.$refs.drawerDireito)
        }
    }
</script>
<style>

</style>


