<template>
    <v-container>
        <v-layout row v-if="error">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>
                <v-card>
                    <v-card-text>
                        <h2 style="color: darkgray">Faca Login</h2>
                    </v-card-text>
                </v-card>

                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onLogin">
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="email"
                                                label="Mail"
                                                prepend-icon="mail"
                                                v-model="email"
                                                type="email"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="password"
                                                label="Password"
                                                prepend-icon="lock"
                                                v-model="password"
                                                type="password"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>

                                <v-layout row>
                                    <v-flex xs12>
                                        <v-btn round to="/">
                                            Cancelar
                                        </v-btn>
                                        <v-btn round type="submit" :disabled="loading" :loading="loading">
                                            Entrar
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>
                                        <!--<v-spacer></v-spacer>-->
                                        <password-reset-dialog></password-reset-dialog>
                                    </v-flex>
                                    <v-flex xs6>

                                        <v-btn flat round @click="googleSignIn" :disabled="loading">

                                            Google

                                        </v-btn>
                                    </v-flex>
                                    <v-flex xs6>

                                        <v-btn flat round @click="facebookSignIn" :disabled="loading">
                                            Facebook
                                        </v-btn>
                                    </v-flex>
                                </v-layout>
                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>

                <v-card>
                    <v-card-text>
                        <p style="color: darkgray">Ainda nao tem conta? <a href="/register">Clique aqui para criar conta</a></p>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                email: '',
                password: '',
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        methods: {
            onLogin () {
                this.$store.dispatch('loginUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            }
        },
        beforeCreate () {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
        },
    }
</script>


