<template>
    <v-container>
        <v-layout row v-if="error">
            <v-flex xs12 sm6 offset-sm3>
                <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
            </v-flex>
        </v-layout>
        <v-layout>
            <v-flex xs12 sm6 offset-sm3>

                <!--Titulo do formulario-->
                <v-card>
                    <v-card-text>
                        <h2 style="color: darkgray">Criacao de conta</h2>
                    </v-card-text>
                </v-card>

                <!--Card com o formulario-->
                <v-card>
                    <v-card-text>
                        <v-container>
                            <form @submit.prevent="onSignup">
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="email"
                                                label="Mail"
                                                prepend-icon="mail"
                                                v-model="email"
                                                type="email"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="password"
                                                label="Password"
                                                prepend-icon="lock"
                                                v-model="password"
                                                type="password"
                                                required></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-text-field
                                                name="confirmPassword"
                                                label="Confirm Password"
                                                prepend-icon="lock"
                                                id="confirmPassword"
                                                v-model="confirmPassword"
                                                type="password"
                                                :rules="[comparePasswords]"></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <v-layout row>
                                    <v-flex xs12>
                                        <v-btn round class="red--text" to="/">
                                            Cancelar
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>
                                        <v-btn round type="submit" class="green--text" :disabled="loading" :loading="loading">
                                            Gravar
                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>
                                    </v-flex>
                                    <v-flex xs6>

                                        <v-btn flat round @click="googleSignIn" :disabled="loading">

                                            Google

                                        </v-btn>
                                    </v-flex>
                                    <v-flex xs6>

                                        <v-btn flat round @click="facebookSignIn" :disabled="loading">
                                            Facebook
                                        </v-btn>
                                    </v-flex>
                                </v-layout>
                            </form>
                        </v-container>
                    </v-card-text>
                </v-card>

                <!--Opcao de login para quem ja possui uma conta-->
                <v-card>
                    <v-card-text>
                        <p style="color: darkgray">Ja possui uma conta? <v-icon>help</v-icon><a href="/login">Clique aqui para login login</a></p>
                    </v-card-text>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { eventBus } from '../../main'
    export default {

        data () {
            return {
                email: '',
                password: '',
                confirmPassword: ''
            }
        },
        computed: {
            comparePasswords () {
                return this.password !== this.confirmPassword
                    ? 'Senha de confirmacao nao corresponde' : ''
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        watch: {
            userAuth (value) {
                if (value !== null && value !== undefined) {
                    this.$router.push('/')
                }
            },
            loading (value) {

                if (!value && !this.error) {

                    let text = "Enviamos um email de confirmacao para si, confirme clicado no link"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color,10000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar

                }
            }

        },
        methods: {
            onSignup () {
                this.$store.dispatch('createUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            }
        },
        beforeCreate () {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
//            this.$Progress.finish()
        },
    }
</script>


