<template>
    <v-dialog v-model="showConfirmDialog" width="400px" style="display: none">
        <v-btn
                fab
                bottom
                right
                fixed
                color="white"
                title="Enviar Sugestoes"
                slot="activator"
                @click.stop="mostrarDialog"
        >
            <v-icon>mail_outline</v-icon>
        </v-btn>
        <v-card>
            <v-card-title class="blue white--text title ">
                {{ showConfirmDialogData.title }}
            </v-card-title>
            <v-container grid-list-sm class="pa-4">
                <v-layout row wrap class="mt-0">
                    <v-flex xs12>
                        <p> {{ showConfirmDialogData.message }} </p>
                    </v-flex>
                </v-layout>
            </v-container>
            <v-divider class="mb-2"></v-divider>
            <v-card-actions class="pt-0">
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">{{ showConfirmDialogData.btn1 }}</v-btn>
                <v-btn flat @click="userConfirmation" class="green--text">{{ showConfirmDialogData.btn2 }}</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    export default {
        data () {
            return {
                dialog: true,
            }
        },
        computed: {
            showConfirmDialogData () {
                return this.$store.getters.globalConfirmDialogDetails
            },
            showConfirmDialog () { //propriedade que mostra ou nao a dialog
                return this.showConfirmDialogData.show && this.dialog ? true : false //redundacia para evitar o erro que lanca no console
            },
        },
        methods: {
            mostrarDialog () {
                this.dialog = !this.dialog
            },
            userConfirmation () {

                this.$store.dispatch('userConfirmation', true)
                this.dialog = !this.dialog

            }
        },
        watch: {
//          Controlando a variavel computada para que ao se fechar a dialog, as propriedades que controlam a dialog sejam resetadas
            showConfirmDialog () {
                if (!this.showConfirmDialog) {
                    this.$store.dispatch('togleConfirmDialog', false)
                    this.dialog = true
                }
            }
        }
    }
</script>


