<template>
    <div class="text-xs-center">
        <v-menu offset-y xs3>
            <!--<v-btn color="primary" dark slot="activator">Dropdown</v-btn>-->
            <v-btn icon large :title="isUserAuth ? userAuth.name : 'Voce nao esta autenticado'" slot="activator">
                <v-avatar size="32px" >
                    <!--Imagem sendo colocada dinamicamente-->
                    <img
                         :src="imgDefault"
                         alt=""
                    >

                </v-avatar>
            </v-btn>
            <v-list>
                <div v-for="item in dropdownItems" :key="item.title">

                    <v-list-tile v-if="item.hasLink" router :to="item.link === 'profile' ? '/' + userAuth.id + '/profile' : item.link">
                        <v-list-tile-action>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                    </v-list-tile>

                    <v-list-tile v-else @click="logOutUser">
                        <v-list-tile-action>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                    </v-list-tile>
                </div>
            </v-list>
        </v-menu>
    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data: () => ({
        }),
        computed: {
            dropdownItems () {
                return this.$store.getters.getDropdownItems
            },
            imgDefault () {
                return this.$store.getters.getImg
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
        },
        watch: {
            userAuth (value) {

                if (value === null) //Se for true, entao o user saiu da sua conta
                {
                    this.notifyUser() //Entao notificamos a ele que ja nao esta mais aut
                }
            }
        },
        methods: {
            logOutUser () {
                this.$store.dispatch('logOutUser')

            },
            notifyUser ()
            {
                let text = "Voce no est mais autenticado!"
                let color = 'blue'
                eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
            }
        }
    }
</script>


