import * as firebase from 'firebase'
import axios from 'axios'
import db_file from '../db'

/**
 * Ficheiro de import de dados das API's Externos para a nossa
 * Os procedimentos:
 *
 * 1- No ficheiro App.vue, chamar o metodo importAPIMozProvinces, para importar as provincias primeiro
 *
 * NOTA: O metodo importAPIMozDistricts deve ser chamado detro de loop dos objectos retornados
 *       pela URL das provincias, dessa forma: dispatch('importAPIMozDistricts', province.id)
 *       Ou seja, ao buscar as provincias vai buscar os distritos tambem
 *
 * 2 - Buscar os distritos do firestore, guardar na memoria da Aplicacao
 *
 * 3 - Por fim pode-se chamar o metodo: importAPIMozPosts, ela vai percorrer o loop baseando-se nos distritos ja buscados
 * NOTA: Esse metodo quando busca os postos administrativos na API Moz, lanca alguns excepcoes de error= 404, isso eh normal.
 *
 *
 */
export default {
    state: {
        urlProvices: 'http://apimoz.herokuapp.com/api/v1.0/provinces?key=bf82ec128a49240cea7d3635e6934382cef63859',
        urlDistricts: 'http://apimoz.herokuapp.com/api/v1.0/provinces/4/districts?',
        urlPosts: ' http://apimoz.herokuapp.com/api/v1.0/provinces/4/districts/42/posts?key=YOUR_APP_KEY',
        apiMozKey: 'bf82ec128a49240cea7d3635e6934382cef63859',
    },
    actions: {
        // Metodo que busca todas as Provincias da API Moz.
        importAPIMozProvinces ({state, commit, dispatch}) {

            const db = firebase.firestore()


            axios.get(state.urlProvices) //Buscando as provincias na API Moz
                .then(response => {

                    response.data.places.forEach(function(province) {

                        let saveProvince = {
                            description: province.description,
                            id: province.id,
                            name: province.name
                        }

                        db.collection('Provinces').doc('' + province.id).set(saveProvince)
                            .then(() => {
                                commit('setProvince', saveProvince) //gravando a provincia no firestore
                            })
                            .catch((error) => {
                                console.log('Ocorreu um erro ao gravar a Provincia no firestore: ', error)
                            })


                        dispatch('importAPIMozDistricts', province.id) //Chamando a action que ira importar os districtos da API Moz.

                    });

                })
                .catch(e => {
                    console.log(e)
                })
        },

        importAPIMozDistricts ({getters, commit, dispatch}, provinceId) {

            const db = firebase.firestore()

            axios.get('http://apimoz.herokuapp.com/api/v1.0/provinces/' + provinceId + '/districts?key=' + state.apiMozKey)
                .then(response => {

                    response.data.places.forEach(function(district) {

                       let distrito = district
                       let Coletion = district.name.toString().startsWith('Distrito ') ? 'DistrictMunicipal' : 'Districts'

                        db.collection(Coletion).doc('' + district.id).set(district)
                            .then(() => {

                                // Guardando as provincias na lista das provincias
                                commit('setDistricts', district)

                            })
                            .catch((error) => {
                                console.log('Ocorreu um erro ao gravar Distritos no firestore: ', error)
                            })

                    });

                })
                .catch(e => {
                    console.log(e)
                })
        },

        importAPIMozPosts ({state, commit}) {

            const db = firebase.firestore() //referencia a firestore

            db_file.state.districts.forEach(function (distrito) { //Percorrendo cada distrito para buscar os seus posts no API MOz.

                // Buscando os Postos administrativos na API Moz
                axios.get('http://apimoz.herokuapp.com/api/v1.0/provinces/' + distrito.in_place.id + '/districts/' + distrito.id + '/posts?key=' + state.apiMozKey)
                    .then(response => {

                        response.data.places.forEach(function(post) {

                            db.collection('Posts').doc('' + post.id).set(post) //gravando o posto na db
                                .then(() => {

                                    // Guardando os postos administrativos na lista dos postos
                                    commit('setPosts', post) //Adicionando o posto administrativo na lista dos posts no db store da

                                })
                                .catch((error) => {
                                    console.log('Ocorreu um erro ao gravar Distritos no firestore: ', error)
                                })

                        });

                    })
                    .catch(e => {
                        console.log(e)
                    })

            })


        },

    }

}




