<template>
    <v-dialog v-model="dialog" width="350px">

        <v-btn
                flat
                icon
                slot="activator"
                @click.stop="dialog = !dialog"
        >
            <v-tooltip bottom>
                <v-icon slot="activator" color="red" style="cursor: pointer">help</v-icon>
                <span>Esqueceu a password? (Clique aqui)</span>
            </v-tooltip>
        </v-btn>
        <v-card>
            <v-card-title
                    class="lighten-4 py-4 title"
            >
                Esqueceu a sua senha?
            </v-card-title>

            <v-divider></v-divider>

            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-layout row wrap>
                    <v-flex xs12 class="mb-3">
                        <v-text-field
                                name="email"
                                label="Seu Email"
                                prepend-icon="mail"
                                v-model="email"
                                type="email"
                                required></v-text-field>
                    </v-flex>

                    <v-flex xs12>
                        <v-icon>info</v-icon>
                        Insira no campo acima, o email que usou para criar a sua conta, iremos lhe enviar um
                        link para poder definir uma nova senha
                    </v-flex>
                </v-layout>

            </v-container>

            <v-divider></v-divider>

            <v-card-actions>
                <v-btn flat color="secundary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="dialog = false">Cancelar</v-btn>
                <v-btn
                    flat
                    @click="onConfirm"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Enviar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                dialog: false,
                email: '',
                showProcess: false,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
        },
        watch: {
            loading (value) {
                if (this.showProcess && !value && !this.error) {

                    let text = "Link enviado com sucesso, clique no link enviado para redefinir a sua senha de email"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.email = ''
                    this.dialog = false
                    this.showProcess = false
                }
            }

        },
        methods: {
            onConfirm () {

                if (this.email === '') {

                    let text = "Insira o seu email..."
                    let color = 'red'
                    this.openSnackBar(text,color)
                    this.showProcess = true
                    return

                }

                this.$store.dispatch('sendUserPasswordResetEmail', {email: this.email})
            },
            onHelp () {

                this.dialog = !this.dialog
                this.showProcess = false
                this.$router.push('/help')

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            openSnackBar (text, color) {
                eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
            }
        }
    }
</script>


