<template>
    <v-app id="inspire">

        <!--Drawer do lado esquerdo.-->
        <v-navigation-drawer
            fixed
            clipped
            app
            disable-route-watcher
            :temporary="temporaryDrawer"
            v-model="drawer"

        >
            <!--Menu Left-->
            <menu-left :temporary="temporaryDrawer"></menu-left>

        </v-navigation-drawer>

        <!--Drawer do lado direito.-->
        <v-navigation-drawer
            fixed
            clipped
            temporary
            right
            v-model="drawerRight"
        >
            <v-list dense>
                <template>

                    <menu-right></menu-right>

                </template>
            </v-list>
        </v-navigation-drawer>

        <!--Barra de cima-->
        <v-toolbar
                color="blue darken-3"
                dark
                app
                clipped-left
                clipped-right
                fixed
        >
            <v-toolbar-title :style="$vuetify.breakpoint.smAndUp ? 'width: 300px; min-width: 250px' : 'min-width: 72px'" class="ml-0 pl-3">
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
                <!--Titulo para dispositivos maiores-->
                <span class="hidden-xs-only" style="cursor: pointer" @click="pushMainPage">{{ title }}</span>
                <!--Titulo para mobile devicess-->
                <span class="hidden-sm-and-up" style="cursor: pointer" @click="pushMainPage">{{ title.substr(0,3) }}</span>
            </v-toolbar-title>
            <v-text-field
                    light
                    solo
                    prepend-icon="search"
                    v-model="search"
                    placeholder="Pesquise pelo nome ou Universidade . . ."
                    style="max-width: 600px; min-width: 128px"
                    class="hidden-xs-only"
            ></v-text-field>
            <div class="d-flex align-center" style="margin-left: auto">
                <v-btn icon class="hidden-sm-and-up">
                    <v-icon>search</v-icon>
                </v-btn>
                <v-menu offset-y xs3>
                    <v-btn icon class="hidden-xs-only" slot="activator">
                        <v-icon>apps</v-icon>
                    </v-btn>
                    <v-list>
                        <div>
                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Centros de Explicacao</v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Bibliotecas </v-list-tile-title>
                            </v-list-tile>

                            <v-list-tile router to="#">
                                <v-list-tile-action>
                                    <v-icon>info</v-icon>
                                </v-list-tile-action>
                                <v-list-tile-title>Material </v-list-tile-title>
                            </v-list-tile>

                        </div>
                    </v-list>
                </v-menu>



                <v-btn icon class="hidden-xs-only">
                    <v-icon>notifications</v-icon>
                </v-btn>

                <!--Opcao do user nao autenticado, vem do componente dropdown-->
                <auth-dropdown></auth-dropdown>

                <v-toolbar-side-icon @click.stop="drawerRight = !drawerRight"></v-toolbar-side-icon>
            </div>
        </v-toolbar>

        <!--Conteudo central-->
        <v-content>

            <!-- O conteudo central sera renderizado aqui -->
            <router-view/>

            <!-- colocando o progressbar que aparece em cima-->
            <vue-progress-bar></vue-progress-bar>

        </v-content>

        <!--Montar aqui a dialog de registo-->
        <template>

            <!--Dialog com as mensagens de confirmacao-->
            <confirm-dialog></confirm-dialog>

            <login-dialog v-if="isHomePage"></login-dialog>
            <home-fab-buttom v-if="isLoginPage || isRegisterPage"></home-fab-buttom>

        </template>

        <!--Rodape da pagina-->
        <v-footer :fixed="fixed" app>
            <span>&copy; Explicador 2018</span>
        </v-footer>

        <!--Snackbar para as notificacoe em baixo-->
        <v-snackbar
            :timeout="snackbarTimeout"
            :color="snackBarColor"
            bottom
            v-model="snackbar"
        >
            {{ snackBarText }}
            <v-btn flat color="pink" @click.prevent="snackbar = false">Fechar</v-btn>
        </v-snackbar>

    </v-app>
</template>

<script>
    import { eventBus } from './main'
    export default {
        name: 'App',
        data: () => ({
            title: 'Explicador',
            search: '',
            drawer: this.route === 'Home' ? this.temporaryDrawer : false, //controlando drawer, para mostrar apenas se estiver na pagina principal
            drawerRight: false,
            fixed: true,
            temporary: false,
            snackbar: false,
            snackbarTimeout: 6000,
            snackBarText: '',
            snackBarColor: 'green'

        }),
        beforeCreate () {

        },
        mounted () {

//            this.$store.dispatch('importAPIMozProvinces') //esse busca provincias e Distritos da API Moz a nossa App
//            this.$store.dispatch('importAPIMozPosts') //Chamar este depois de buscar localmente as districts

//            Escuta de evento que fecha os drawers esquerdo ou direito
            eventBus.$on('closeDrawer', (position) => {

                if (position === 'left'){
                    this.drawer = !this.drawer
                }
                if (position === 'right'){
                    this.drawerRight = !this.drawerRight
                }

            })
            eventBus.$on('ocultarMenus',  (value) => {

                this.drawer = value
                this.drawerRight = value

            })
            eventBus.$on('showSnackbar', (text, color, timeout) => {

                this.snackBarColor = color
                this.snackBarText = text
                this.snackbarTimeout = timeout
                this.snackbar = true

            })
            eventBus.$on('semNet', () => {

                this.semNet()

            })

        },
        computed: {
            isSideMenusShowing () {
                return this.$store.getters.isSideMenusShowing
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            isLoginPage () {
                return this.$store.getters.isLoginPage
            },
            isRegisterPage () {
                return this.$store.getters.isRegisterPage
            },
            route () {
                return this.$route.name
            },
            temporaryDrawer () {
                switch (this.$vuetify.breakpoint.name) {
                    case 'xs':
                    case 'sm':
                    case 'md': return true
                    case 'lg':
                    case 'xl': return false
                }
            }
        },
        watch: {
            // verificando a rota actual, para que ao sair do perfil, a variavel que controla o user que se esta a visualizar o seu perfil, fique nula.
            route (value) {
                this.$store.dispatch('setProfileUserNull')

                console.log('a rota mudou')
                if (value !== 'Home' && this.drawer)
                {
                    this.drawer = false

                } else if (value === 'Home') {

                    this.drawer = true

                }


            },
            // value = true, significa que eh temporario, oculta o menu para os dispositivos menores
            // value = false, significa ficar sempre visivel, mostrar o drawer nos dispositivos maiores como laptop
            temporaryDrawer (value)
            {

                this.drawer = !value //Se o drawer for temporario, vamos esconder. Caso o user quiser visualizar vai clicar no mesmo.
                if (this.route !== 'Home')
                {
                    this.drawer = false
                }
            },
//          Pesquisa implementada aqui. Quando se pesquisa, um evento eh emitido e processado no ficheiro Home. veja o evento $on('searchForExplicador)
//            No ficheiro home.
            search (value) {

                eventBus.$emit ('searchForExplicador', value)

            }
        },
        methods: {
            pushMainPage () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
                eventBus.$emit('resetExplicadoresFilter');
                this.$router.push('/')
            },
            semNet () {

                let title = 'Sem conexo a internet'
                let message = 'Vo no tem acesso a internet, verifique a sua conexo ou clique a tecla F5 para refrescar a pagina, .. .'
                const btn1 = 'Fechar'
                const btn2 = 'Ok'

//              Chamando a tela de confirmacao global, para informar o user que ele nao esta autenticado
                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
            }

        }
    }
</script>

<style>
    /* Estilo para adicionar pading nos botoes de add que chamam as dialogs no perfil*/
    @media screen and (max-width: 900px) {
        .personalized-pr {
            padding-right: 5px !important;
        }
    }

    @media screen and (max-width: 400px) {
        .personalized-pr {
            padding-right: 35px!important;
        }
    }

    /*Estilo para o icon de processamento nos botoes de registo*/
    .custom-loader {
        animation: loader 1s infinite;
        display: flex;
    }
    @-moz-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-webkit-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @-o-keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
    @keyframes loader {
        from {
            transform: rotate(0);
        }
        to {
            transform: rotate(360deg);
        }
    }
</style>


