<template>
    <v-flex>
        <v-card class="pb-2">

            <!--Div que adiciona um padding-top para a imagem-->
            <div class="pt-exp"></div>

            <v-badge
                 v-if="user.provider !== 'password' || user.confirmed"
                 left
                 title="User confirmado"
                 color="green"
                 class="badje-adjust" overlap>
                <v-icon slot="badge" dark small>done</v-icon>
            </v-badge>

            <v-card-media
                :src="img"
                height="150px"
                class="img-radius"
            >
            </v-card-media>

            <!--Div que adiciona um padding entre a imagem e nome do utilizador, mas so fica visiveis no dispositivos menores-->
            <div class="pt-exp hidden-sm-and-up"></div>
            <!--Titulo do explicador-->

                <!--<span title="6 pontos" slot="badge" style="cursor: pointer">6</span>-->
                    <v-card-title primary-title class="mt-4">
                        <div>
                            <div
                                 class="headline" :class="nomeUser !== 'Sem Nome' ? '' : ' red--text' ">
                                {{ nomeUser }}
                            </div>
                            <span
                                class=""
                                :class="userInstitution === 'Sem Universidade (Escola)' ? 'red--text' : 'grey--text'"
                            >{{ userInstitution }}</span>
                        </div>
                    </v-card-title>

            <!--Botoes para acoes-->
            <v-card-actions>

                <v-btn flat small color="purple" :to="'/' + user.id + '/profile/'" title="Ver perfil">Perfil</v-btn>
                <v-spacer></v-spacer>
                <v-tooltip top>
                    <v-btn icon slot="activator" @click.native="show = !show">
                        <v-icon>{{ show ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
                    </v-btn>
                    <span>Veja o que ele diz sobre ele</span>
                </v-tooltip>

                <!--Menu que aparece num dos botoes-->
                <v-menu bottom left>
                    <v-btn icon ripple slot="activator">
                        <v-icon>more_vert</v-icon>
                    </v-btn>

                    <!--Opcoes de menu que aparecem depois de click-->
                    <v-list>
                        <v-tooltip right>
                            <v-list-tile slot="activator" @click="">
                                <v-list-tile-title>Ver perfil</v-list-tile-title>
                            </v-list-tile>
                            <span>Perfil o perfil completo deste explicador</span>
                        </v-tooltip>

                        <v-tooltip right>
                            <v-list-tile slot="activator" @click="">
                                <v-list-tile-title>Disciplinas</v-list-tile-title>
                            </v-list-tile>
                            <span>Disciplinas que ele explica</span>
                        </v-tooltip>

                        <v-tooltip right>
                            <v-list-tile slot="activator" @click="">
                                <v-list-tile-title>Marcar</v-list-tile-title>
                            </v-list-tile>
                            <span>Marcar como explicador favorito</span>
                        </v-tooltip>

                        <v-tooltip right>
                            <v-list-tile slot="activator" @click="onHelp">
                                <v-list-tile-title>Help</v-list-tile-title>
                            </v-list-tile>
                            <span>Descubra mais</span>
                        </v-tooltip>

                    </v-list>
                </v-menu>

            </v-card-actions>
            <v-slide-y-transition>
                <v-card-text v-show="show">


                    <v-list v-if="user.Educations.subjects.length > 0">
                        <template v-for="(s, i) in user.Educations.subjects">
                            <v-subheader v-if="i === 0">Disciplinas</v-subheader>
                            <!--<v-divider inset="true"></v-divider>-->
                            <v-list-tile @click="">
                                <v-list-tile-content :title="s.name">
                                    <v-list-tile-title v-html="s.name"></v-list-tile-title>
                                </v-list-tile-content>
                            </v-list-tile>

                        </template>
                    </v-list>
                    <div v-else>
                        {{ user.bio }}
                    </div>


                </v-card-text>
            </v-slide-y-transition>
        </v-card>
    </v-flex>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user'],
        data: () => ({
            show: false
        }),
        mounted () {

        },
        computed: {
            img () {
                return this.user !== undefined ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.user.name !== null ? this.user.name : 'Sem Nome'
            },
            userInstitution () {
                let inst = this.user.Educations.institution
                return inst !== null && inst !== '' ? inst : 'Sem Universidade (Escola)'
            }
        },
        methods: {

            onHelp () {
                this.$store.dispatch('setShowHelpExplicador', true)
            }

        }
    }
</script>
<style scoped>
    .headline {
        font-size: 18px!important;
        font-weight: 400;
        line-height: 32px!important;
        letter-spacing: normal!important;
    }
    .img-radius {
        display: block;
        margin-left: auto;
        margin-right: auto;
        border-radius: 100%;
        width: 90px;
        height: 90px!important;
    }

    .pt-exp {
        padding-top: 15px;
    }

    .badje-adjust {
        background: red!important;
        padding-left: 10px;
        margin: 2px 1px 1px 15px
    }

    @media screen and (max-width: 595px) {
        .img-radius {
            display: block;
            margin-left: auto;
            margin-right: auto;
            border-radius: 100%;
            width: 110px;
            height: 110px!important;
        }
    }

</style>


