import * as firebase from 'firebase'
import 'firebase/firestore'
import auth_store from './auth/index'
export default {
    state: {
        posts: [],
        districts: [],
        provinces: [],
        bairros: [],
        institutions: [],
        courses: [],
        subjects: [],
    },
    mutations: {

        setProvince (state, value) {
            state.provinces.push(value)
        },

        setDistrict (state, value) {
            state.districts.push(value)
        },

        setBairro (state, value) {

            let bairro = value.data()
            bairro.id = value.id

            state.bairros.push(bairro)

        },

        setPost (state, value) {
            state.posts.push(value)
        },

        setInstitution (state, value) {
            state.institutions.push(value)
        },

        setCourse (state, value) {
            state.courses.push(value)
        },

        setSubject (state, value) {
            state.subjects.push(value)
        },

    },
    getters: {
        getDistricts (state) {
            return state.districts
        },
        getInstitutions (state) {
            return state.institutions
        },
        getCourses (state) {
            return state.courses
        },
        getSubjects (state) {
            return state.subjects
        },

    },

    actions: {

        sendSugestion ({commit}, value) {

            commit('setLoading',true)
            commit('clearError')

            const db = firebase.firestore()
            let sugestion_id = null

            // Gravando a sugestao
            db.collection("Sugestions").add({
                    sugestion: value.sugestion,
                    creatorId: value.creatorId,
                    created_at:  firebase.firestore.FieldValue.serverTimestamp(),
                    imageUrl: ''
                })
                .then( objSaved => {

                    if (value.image === null || value.image === undefined) return //verificando se ha alguma imagem ou nao

                    sugestion_id = objSaved.id //guardando o id da sugestao gravada
                    const filename = value.image.name
                    const ext = filename.slice(filename.lastIndexOf('.'))
                    return firebase.storage().ref('SugestionImages/' + objSaved.id + '.' + ext).put(value.image) //gravando a imagem com o nome como id da sugestao
                })
                .then(fileData => {

                    if (fileData === null || fileData === undefined) return

                    let imageUrl = fileData.metadata.downloadURLs[0]
                    return db.collection("Sugestions").doc(sugestion_id).update({imageUrl: imageUrl})

                })
                .then(() => {
                    // gravar aqui na lista das sugestoes
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        saveInstitution ({commit,state}, value) {

            commit('setLoading',true)
            commit('clearError')

            // verificando se esta instituicao ja existe gravado, para nao gravar novamente
            const instit = state.institutions.find((inst) => {
                return inst.name === value.name
            })

            if (instit !== undefined) return

            const db = firebase.firestore()
            // Gravando a nova Instituicao
            db.collection("Institutions").add(value)
                .then( () => {

                    commit('setInstitution', value)
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        saveCourse ({commit,state}, value) {

            commit('setLoading',true)
            commit('clearError')

            // verificando se este curso ja existe gravado, para nao gravar novamente
            const c = state.courses.find((course) => {
                return course.name === value.name
            })

            if (c !== undefined) return

            const db = firebase.firestore()
            // Gravando o novo Curso no firestore
            db.collection("Courses").add(value)
                .then( () => {

                    commit('setCourse', value)
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        saveSubject ({commit, state}, value) {

            commit('setLoading',true)
            commit('clearError')

            // verificando se esta disciplina ja existe gravado, para nao gravar novamente
            const s = state.subjects.find((subject) => {
                return subject.name === value.name
            })

            if (s !== undefined) return

            const db = firebase.firestore()
            // Gravando a nova disciplina no firebase
            db.collection("Subjects").add(value)
                .then( () => {

                    commit('setSubject', value)
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        editSubject ({commit, state, dispatch}, value) {

            commit('setLoading',true)
            commit('clearError')

            // verificando se esta disciplina ja existe gravado, para nao gravar novamente
            const s = state.subjects.find((subject) => {
                return subject.name === value.name
            })

            if (s === undefined) return //a disciplina nao existe para edicao

            s.name = value.newName //Gravando o novo name

            const db = firebase.firestore()
            // Gravando a nova disciplina no firebase
            db.collection("Subjects").doc(s.id).set(s)
                .then( () => {

                    dispatch('getAllSubjects')
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        saveBairro ({commit, state}, value) {

            commit('setLoading',true)
            commit('clearError')

            // verificando se esta disciplina ja existe gravado, para nao gravar novamente
            const s = state.bairros.find((b) => {
                return b.name === value.name
            })

            if (s !== undefined) return

            const db = firebase.firestore()
            // Gravando a nova disciplina no firebase
            db.collection("Bairros").add(value)
                .then( () => {

                    commit('setBairro', value)
                    commit('setLoading',false)

                })
                .catch((error) => {
                    console.log(error)
                    commit('setLoading',false)
                    commit('setError', error)
                })
        },

        getAllProvinces ({commit}) {

            const db = firebase.firestore()

            db.collection("Provinces").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        commit('setProvince',  doc.data())

                    });


                })
                .catch( (error => {
                    console.log('erro buscando a lista das Provincias', error)
                }))


        },

        getAllBairros ({commit}) {

            const db = firebase.firestore()

            db.collection("Bairros").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        commit('setBairro',  doc)

                    });
                })
                .catch( (error => {
                    console.log('erro buscando a lista dos bairros', error)
                }))
        },

        getAllDistricts ({commit}) {

            const db = firebase.firestore()

            db.collection("Districts").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        commit('setDistrict',  doc.data())

                    });

                    // dispatch('importAPIMozPosts') //Buscando as Postos da API Moz, apenas no inicio


                })
                .catch( (error => {
                    console.log('erro buscando a lista dos distritos', error)
                }))


        },

        getAllPosts ({commit}) {

            const db = firebase.firestore()

            db.collection("Posts").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        commit('setPost',  doc.data())

                    });

                    // dispatch('importAPIMozPosts') //Buscando as Postos da API Moz, apenas no inicio


                })
                .catch( (error => {
                    console.log('erro buscando a lista dos postos Administrativos', error)
                }))


        },

        getAllInstitutions ({commit, state}) {

            const db = firebase.firestore()

            if (state.institutions.length > 0) return

            db.collection("Institutions").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        let data = doc.data()
                        data.id = doc.id
                        commit('setInstitution',  data)

                    });

                    // dispatch('importAPIMozPosts') //Buscando as Postos da API Moz, apenas no inicio


                })
                .catch( (error => {
                    console.log('erro buscando a lista dos postos Administrativos', error)
                }))


        },

        getAllCourses ({commit, state}) {

            const db = firebase.firestore()

            if (state.institutions.length > 0) return

            db.collection("Courses").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        let data = doc.data()
                        data.id = doc.id
                        commit('setCourse',  data)

                    });

                    // dispatch('importAPIMozPosts') //Buscando as Postos da API Moz, apenas no inicio


                })
                .catch( (error => {
                    console.log('erro buscando a lista dos postos Administrativos', error)
                }))


        },

        getAllSubjects ({commit, state}) {

            const db = firebase.firestore()

            if (state.institutions.length > 0) return

            db.collection("Subjects").get().then(function(querySnapshot) {

                    querySnapshot.forEach(function(doc) {

                        let data = doc.data()
                        data.id = doc.id
                        if (!data.hasOwnProperty('confirmed') || !data.hasOwnProperty('creatorId'))
                        {
                            data.confirmed = false
                            data.creatorId = auth_store.state.userAuth !== null ? auth_store.state.userAuth.id : ''
                            data.created_at = new Date()

                        }
                        commit('setSubject',  data)

                    });

                })
                .catch( (error => {
                    console.log('erro buscando as disciplinas', error)
                }))


        },

    }

}


