<template>
    <!--Informacoes do perfil-->
    <v-card>

        <v-card-title style="color: gray"><h3>Informacoes Pessoais</h3></v-card-title>

        <v-container grid-list-sm class="pa-4">

            <!--Aqui serao mostrados os erros-->
            <v-layout row v-if="error">
                <v-flex xs12 sm6 offset-sm3>
                    <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                </v-flex>
            </v-layout>


            <!--Campos do formulario-->
            <v-layout row wrap>

                <!--Campo nome-->
                <v-flex xs12 sm6 align-center justify-space-between>
                    <v-layout align-center>

                            <!--Esse vai colocar um elemento img para ao clicar abrir a dialog de upload de foto-->
                            <profile-photo-upload :user="user"></profile-photo-upload>

                        <v-text-field
                            placeholder="Seu nome"
                            v-model="name"
                            :rules="nameRules"
                            type="text">
                        </v-text-field>
                    </v-layout>
                </v-flex>

                <!--Campo email-->
                <v-flex xs12 sm6>
                    <v-text-field
                            prepend-icon="mail"
                            v-model="email"
                            disabled
                            placeholder="Seu Email">
                    </v-text-field>
                </v-flex>

                <!--Campo descricao-->
                <v-flex xs12>
                    <v-text-field
                        prepend-icon="face"
                        v-model="bio"
                        placeholder="Fale um pouco de si">
                    </v-text-field>
                </v-flex>

                <!--Campo telfone-->
                <v-flex xs12 sm6>
                    <v-text-field
                            type="number"
                            prepend-icon="phone"
                            placeholder="841234567"
                            v-model="telfone"
                            :rules="telfoneRules"
                            :counter="9"
                    ></v-text-field>
                </v-flex>

                <!--Campo sexo -->
                <v-flex xs12 sm6>
                    <v-text-field
                        prepend-icon="account_box"
                        v-model="gender"
                        placeholder="Seu Sexo"
                    ></v-text-field>
                </v-flex>

            </v-layout>

        </v-container>

        <!--Botoes do formulario-->
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat v-if="isMyProfile" color="primary" @click="onCancel">Cancel</v-btn>

            <v-btn v-if="isMyProfile" flat @click="onSave" :disabled="loading" :loading="loading && showPersonalProcess">
                Gravar
                <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                </span>
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user'],
        data () {
            return {
                name: '',
                nameRules: [
                    (v) => !!v || 'O seu nome  necessrio ',
                    (v) => v && v.length >= 2 || 'O seu nome nao pode ter menos de 2 caracteres'
                ],
                email: '',
                gender: '',
                bio: '',
                telfone: '',
                telfoneRules: [
                    (v) => v && v.length <= 9 || 'O seu numero de celular deve ter 9 digitos',
                ],
                showPersonalProcess: false,
                canceled: false,
            }
        },
        watch: {
            user (value) {

                if (value !== null && value !== undefined) {

                    this.inicializeUserInfo()
                }
            },
            loading (value) {

                if (!value && !this.error && !this.canceled && this.showPersonalProcess) {

                    let text = "Os seus dados de Identificacao foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showPersonalProcess = false
                }
            }

        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            userIsAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        methods: {
            onSave () {

                //Procurar outra forma segura de verificar se o user tem net...
                if (!this.userAuth.hasOwnProperty('bio')) {

                    eventBus.$emit('semNet');

                    return
                }

                let user = this.userAuth
                user.name = this.name
                user.email = this.email
                user.gender = this.gender
                user.bio = this.bio
                user.telfone = this.telfone

                this.showPersonalProcess = true
                this.$store.dispatch('updateOnlineUserData', user)

            },
            onCancel () { //Este vai ser chamado antes de se comear a gravar, para ver se algum dado foi modificado...
                this.$store.dispatch('setLoading', false)
                this.canceled = true
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            inicializeUserInfo () {

                const user = this.user
                this.name = this.isUser ? user.name : ''
                this.email = this.isUser ? user.email : ''
                this.gender = this.isUser ? user.gender : ''
                this.bio = this.isUser ? user.bio : ''
                this.telfone = this.isUser ? user.telfone : ''

            }
        },
        beforeCreate () {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {

            if (this.isUser) {

                this.inicializeUserInfo()

            }
        }
    }
</script>


