<template>
    <!--Informacoes do perfil-->
    <v-card class="mt-3">

        <v-card-title style="color: gray"><h3><i class="fas fa-street-view  green--text"></i> Informacoes de Localizacao</h3></v-card-title>

        <v-container grid-list-sm class="pa-4">

            <!--Aqui serao mostrados os erros-->
            <v-layout row v-if="error">
                <v-flex xs12 sm6 offset-sm3>
                    <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                </v-flex>
            </v-layout>


            <!--Campos do formulario-->
            <v-layout row wrap>

                <!--Campo Provincia-->
                <v-flex xs12>
                    <v-text-field
                        prepend-icon="account_balance"
                        v-model="province"
                        placeholder="Sua Provincia">
                    </v-text-field>
                </v-flex>

                <!--Campo Curso -->
                <v-flex xs12 sm12>
                    <v-text-field
                        prepend-icon="dns"
                        v-model="city"
                        type="text"
                        placeholder="Sua Cidade"
                    ></v-text-field>
                </v-flex>

                <!--Campo disciplinas-->
                <v-flex xs12 sm12>
                    <v-text-field
                        prepend-icon="account_box"
                        v-model="bairro"
                        type="text"
                        placeholder="Seu bairro"
                    ></v-text-field>
                </v-flex>

            </v-layout>

        </v-container>

        <!--Botoes do formulario-->
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn v-if="isMyProfile" flat color="primary" @click="">Cancel</v-btn>

            <v-btn v-if="isMyProfile" flat justify @click="onSave" :disabled="loading" :loading="loading && showLocationProcess">
                Gravar
                <span slot="loader" class="custom-loader">
                    <v-icon light>cached</v-icon>
                </span>
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user'],
        data () {
            return {
                province: '',
                city: '',
                bairro: '',
                showLocationProcess: false
            }
        },
        watch: {
            user (value) {
                if (value !== null && value !== undefined) {
                    this.inicializeUserInfo()
                }
            },
            loading (value) {

                if (!value && !this.error && this.showLocationProcess) {

                    let text = "Os seus Dados de localizacao foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showLocationProcess = false
                }
            }

        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            userIsAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        methods: {
            onSave () {

                //Procurar outra forma segura de verificar se o user tem net...
                if (!this.userAuth.hasOwnProperty('bio')) {

                    eventBus.$emit('semNet'); //Chamando o metodo  global para notificar a falta de nota

                    return
                }

                let user = this.userAuth

                user.Locations = {
                    province:    this.province,
                    city:        this.city,
                    bairro:      this.bairro,
                }

                this.showLocationProcess = true
                this.$store.dispatch('updateOnlineUserData', user)

                //Gravando o bairro registado, caso tiver sido registado.
                if (this.bairro === '' || this.bairro.toString().length  < 2)  return

                this.$store.dispatch('saveBairro', {
                    name:               this.bairro,
                    creatorId:          user.id,
                    created_at:         new Date(),
                    pronviceId:         '',
                    urbanDistrictId:    '',
                    confirmed:          false,
                })

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            inicializeUserInfo () {

                let user = this.user
                this.province = user.Locations ? user.Locations.province : ''
                this.city = user.Locations ? user.Locations.city : ''
                this.bairro = user.Locations ? user.Locations.bairro : ''

            }
        },
        beforeCreate () {
            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {

            if (this.user !== null && this.user !== undefined) {

                this.inicializeUserInfo()

            }
        }
    }
</script>


